.selectBox {
    display: flex;
    flex-direction: row;
}

#slct {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    outline: 0;
    box-shadow: none;
    background: #000;
    background-image: none;
    flex: 1 1;
    padding: 0 0.5em;
    color: #fff;
    cursor: pointer;
    border: 0;
    font-size: 1em;
}

/* Remove IE arrow */
#slct::-ms-expand {
    display: none;
    border: 1px white solid;
}
/* Custom Select */
.select {
    position: relative;
    display: flex;
    width: 10vw;
    min-width: 130px;
    height: 2em;
    line-height: 2;
    background: #2c3e50;
    border: 2px white solid;
    margin-right: 10px;
}

/* Arrow */
.select::after {
    content: '\25BC';
    position: absolute;
    top: 0;
    right: 0;
    padding: 0 1em;
    background: #000;
    color: #fff;
    cursor: pointer;
    pointer-events: none;
    transition: 0.25s all ease;
}
/* Transition */
.select:hover::after {
    color: #f39c12;
}

.input {
    border: 3px white solid;
    min-width: 300px;
    padding-right: 10px;
}

.input > input {
    width: 100%;
    min-width: 300px;
    height: 2em;
    border: 0;
    padding: 0;
    padding-left: 10px;
    line-height: 2;
    background-color: #000;
    color: white;
    font-size: 1em;
}

@media (max-width: 800px) {
    #searchBar {
