        justify-items: flex-start;
        align-items: flex-start;
        margin-left: 10%;
        width: 90%;
    }

    .selectBox {
        width: 100%;
        flex-wrap: wrap;
    }

    .select {
        width: 20%;
        margin-bottom: 10px;
    }

    .input {
        width: 80%;
        margin-bottom: 10px;
        min-width: 250px;
    }
    .input > input {
        width: 100%;
        min-width: 0px;
    }
}

@media (max-width: 530px) {
