    font-size: 4vmin;
}
.textDetail {
    display: flex;
    width: 100%;
    font-size: 2.5vmin;
}
.textDetail > h3 {
    margin: 0.5% 0;
    width: 200px;
}
#underlineDetail {
    width: 70vmin;
    height: 0.3vh;
    background-color: white;
}
