#showcase {
    display: flex;
    min-height: 500px;
    flex-wrap: wrap;
    padding-bottom: 100px;
    width: 100%;
    justify-content: center;
}
#video {
    display: flex;
    flex-direction: column;
    width: 900px;
}
#gallaryShowcase {
    width: 900px;
}

.gallary {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    width: 100%;
}
.imageShowcase {
    width: 400px;
    margin-bottom: 1%;
}
#titleShowcase {
    width: 100%;
    display: flex;
}
#videoShowcase {
    width: 90%;
    min-height: 400px;
    align-self: center;
}
.titleShowcase-t {
    color: white;
    height: 5vh;
    font-size: 4vmin;
    margin-left: 5%;
    padding: 20px 0;
}

